<template>
    <yd-layout class="box_ziliao">
        <h1>{{title}}</h1>
        <div class="content">{{content}}</div>
    </yd-layout>

</template>

<script>
    export default {
        name: "ziliaocon",
        data:function () {
            return{
                title:window.localStorage.getItem('title'),
                content:window.localStorage.getItem('content'),
            }
        },
    }

</script>

<style scoped>
    h1{
        text-align: center;
        font-size: 0.4rem;
        line-height: 1rem;
    }
    .content{
        font-size: 0.3rem;
        line-height: 0.6rem;
    }
    .box_ziliao{
        padding: 0.4rem;
        background-color: #ffffff;
    }
</style>



